/*公共样式 请误修改*/
*{margin: 0; padding: 0;}
html {margin:0;padding:0;font:10px,'Hiragino Sans GB', 'Microsoft YaHei', '微软雅黑', '宋体', sans-serif; color: #4D4D4D;}
body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, textarea, p, blockquote, th, td, span {margin:0;padding:0;}
html,body{ background:#f5f5f5; font-size:10px; font-family: Helvetica, 'Hiragino Sans GB', 'Microsoft Yahei', '微软雅黑', Arial, sans-serif;}
/*a*/
a {text-decoration: none;color:#4D4D4D;}
a:hover, a:focus {text-decoration: none;}
/*table*/
table {font-size: inherit;}
table {border-collapse: collapse;border-spacing: 0;}
fieldset, img {vertical-align: middle;border: 0;-ms-interpolation-mode: bicubic;/*IE专用*/}
/*ul li dl dt */
ul, li, ol, dl, dt, dd {list-style: none outside none;}
/*h1 h2 q em th sub strong*/
h1, h2, h3, h4, h5, h6 {font-size: 100%;font-weight: normal;}
address, caption, cite, code, dfn, em, strong, th, var {font-style: normal;font-weight: normal;}
caption, th {text-align: left;}
abbr, acronym {border:none;font-variant: normal;}
sup {vertical-align: text-top;}
sub {vertical-align: text-bottom;}
pre, code, kbd, samp, tt {font-family: monospace;line-height: 100%;}
strong {font-weight: bold;}
/*input*/
input, textarea, select {font-family: inherit;font-size: inherit;font-weight: inherit;vertical-align:middle;outline:none;}
input, textarea, select, legend {color: #000;}
input, select, button, textarea {font: 99% arial, helvetica, clean, sans-serif;}
input[type=button], input[type=submit] {-webkit-appearance: button;cursor: pointer;}
button{line-height:1;font-family:inherit;cursor:pointer;*overflow:visible;}
img{max-width: 100%;display:inline-block; height: auto; margin: 0 auto;}
/*清除浮动*/
.cl{zoom:1;clear: both}
.cl:before,.cl:after{content:'';display:table}
.cl:after{clear:both}
.fl {float: left;}
.fr {float: right;}
.hide {display: none;}
[v-cloak] {display: none !important;}

.blue{color: #3db6f2;}

/**处理间距**/
.mb0{margin-bottom: 0 !important;}

/**按钮**/
.btn{position: relative; height:4rem; margin: 0 auto; line-height:4rem; display:block; overflow: hidden; font-size: 1.8rem; width: 100%;text-align: center; box-sizing: border-box;     color: #656b79;
    background-color: #f6f8fa;
    box-shadow: 0 0 1px #b8bbbf;}
.btn-primary{width:95%; border-radius: 5px; margin:3rem auto; background-color:#3db6f2; color: #fff;}


/**处理赖加载的样式**/
.lazy-img-fadein[lazy=loaded] {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}
.lazy-img-fadein[lazy=loading] {
  width: 20px !important;
  margin:25px auto;
}
.lazy-img-fadein[lazy=error] {
  width:40px !important;
  margin: 0 auto;
}
.lazy-img-fadein[lazy=error] {
  border-radius: 2px;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}

