.coupons-list .list-body .list-body-title{ padding: 10px; font-size: 1.4rem;}
.coupons-list .list-body .list-body-typoe,.coupons-list .list-body .list-body-explain{width: 100%; float: right; text-align: right; padding-right: 1rem;font-size:1.6rem;}
.coupons-list .list-body .list-body-typoe .size-typoe{font-size:3rem; margin-right:1.2rem;}
.coupons-list .list-body .list-body-explain{font-size:1.4rem; margin-bottom:1rem}
.coupons-list .list-bottom{margin: 0; padding:1rem 1rem; overflow: hidden; background: #fff;}
.coupons-list .list-bottom .first{float:left;text-align: left;}
