.list-body .ico-log {width:65px; left:2.5rem;position:absolute;}
.list-body .ico-log img {width:100%;height:100%;}

/**选择车型**/
.u-search{position: absolute;top:0;left:0; width: 100%;}
.u-search .mint-search-list{z-index:999;top:50px;padding-top:0; background: #fff;}
.VehicleModel .page-indexlist{padding-top:4rem}
/**右侧弹出**/
.g-popup{background: #fff; margin-top: 4rem; width:40%; height: 100%; border-left: 1px solid #eee;}
.select-vehicle-title{width:70%; display:inline-block; padding: 0 5px; height:40px; line-height:40px; box-sizing: border-box; border-bottom: 1px solid #eee; overflow: hidden; text-overflow: ellipsis; white-space: nowrap;}
.select-vehicle-ico{ width: 30%;display:inline-block; float: right;height:40px; line-height:40px; background: #eee; text-align: center;border-bottom: 1px solid #eee;overflow: hidden; }
.select-vehicle-ico .iconfont{color: #fff;}
.select-vehicle-con{ width:100%;}
.select-vehicle-con ul {padding-left: 1rem;height:500px; overflow: auto;}
.select-vehicle-con ul li{border-bottom: 1px solid #eee; height: 30px; line-height: 30px; width: 100%; overflow: hidden; text-overflow: ellipsis; white-space: nowrap;}

/**弹出框**/
.modal-mask {position: fixed;z-index: 9998;top: 0;left: 0; width: 100%;height: 100%;background-color: rgba(0, 0, 0, .5);display: table;transition: opacity .3s ease;}
.modal-wrapper {display: table-cell;vertical-align: middle; padding: 0 1rem; box-sizing: border-box;}
.modal-container {box-sizing: border-box;min-width:300px;margin:0px auto;background-color:#fff;border-radius:5px;box-shadow: 0 2px 8px rgba(0, 0, 0, .33);transition: all .3s ease;font-family: Helvetica, Arial, sans-serif; overflow: hidden;}
.modal-header h3 {color: #fff;width: 100%; font-size: 1.4rem; text-align: center;}
.modal-header-stop{position: absolute; top:-0.5rem;right: 1rem;}
.modal-header-stop .iconfont{color: #fff;font-size:2.4rem;}
.modal-body {margin: 20px 0; box-sizing: border-box;}
.modal-enter {opacity: 0;}
.modal-leave-active {opacity: 0;}
.modal-enter .modal-container,
.modal-leave-active .modal-container {-webkit-transform: scale(1.1);transform: scale(1.1);}
.modal-header{
	  position: relative;
    height:50px;
    line-height:50px;
    box-sizing:border-box;
    text-align:center;
    position:relative;
    background: #7ac7ed; /* Old browsers */
    background: -moz-linear-gradient(left,  #7ac7ed 1%, #5accbd 100%, #7db9e8 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(left,  #7ac7ed 1%,#5accbd 100%,#7db9e8 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right,  #7ac7ed 1%,#5accbd 100%,#7db9e8 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#7ac7ed', endColorstr='#7db9e8',GradientType=1 ); /* IE6-9 */
}
.modal-default-button{ width:100%; color: #fff; height:3rem; line-height: 3rem; font-size: 1.6rem; display:block; margin:0 auto;text-align:center; background:#7ac7ed; border:0}


/**领券成功弹出提示内容**/
.ticket-list-succeed {position:relative;width:90%; margin:0 auto  20px;box-sizing:border-box;padding:0;box-shadow:0 5px 10px #eee;background:#fff;overflow:hidden;border-radius:5px;}
.ticket-list-succeed .succeed-left {width:100%;position:absolute;top:0;left:0; text-align:left;padding:10px; box-sizing: border-box;}
.ticket-list-succeed .succeed-left h6 {margin-bottom:1.5rem; font-size: 1.6rem;}
.ticket-list-succeed .succeed-left p { margin-bottom:0.5rem;}
.succeed-right {float:right;width:127px;height:110px;}
.succeed-right img{width:100%;height:auto;}
.modal-default-ticket{ width: 90%; margin:1rem auto; background: #3db6f2; border-radius: 5px;}

/**填加洗车卡提示内容**/
.card-active {padding:10px;}
.card-active input {background:#f4f4f4; width: 100%; margin: 0 auto; border: 0; border-radius:5px; height:50px;line-height:50px;padding:5px;margin-bottom:1rem; box-sizing: border-box;}
.card-active p{color:red;}

/**点击领券**/
.m-wash-car-alert {width:100%;color:#000;box-sizing:border-box; padding: 0 1rem;}
.m-wash-car-alert p {line-height:28px;font-size:1.5rem;}
.m-wash-car-alert .m-hint {font-size:1.6rem;padding:1rem 0;box-sizing:border-box}
.m-wash-car-alert .m-hint span { font-size:1.6rem;}
.m-wash-car-alert .weui-btn { width:90% }
.m-wash-car-alert .weui-btn-default{ color:#4e4e4e; background:#fff; border:1px solid #3db6f2}


/**我知道提示图片**/
.know-carcard {position:fixed; height:100%;z-index:999;top:3rem;left:0;width:100%;box-sizing:border-box;padding:30px 10px;text-align:center;background-color: rgba(0, 0, 0, .5);}
.know-carcard img.hand {width:60%}
.know-carcard img.know{width:30%;margin-top:13%;display:block}
.card-active {padding:10px;}

/*数字步骤描述*/
.steps-title {width:90%;margin:2rem auto;}
.steps {position: relative;counter-reset: step;  /*创建步骤数字计数器*/
  z-index:0; margin-bottom:8rem;}
.steps li {list-style-type: none;font-size: 12px;text-align: center;width: 33.33%;position: relative;float: left;color:#999;}
 
/*步骤数字*/
.steps li:before {display: block;
  content: counter(step); /*设定计数器内容*/
  counter-increment: step;  /*计数器值递增*/
  width: 32px;height: 32px;background-color: #3db6f2;line-height: 32px;border-radius: 32px;font-size: 16px;color: #fff;text-align: center;margin: 0 auto 8px auto;}
 
/*连接线*/
.steps li ~ li:after {content: '';width: 100%;height: 2px;background-color: #019875;position: absolute;left: -50%;top: 15px;z-index:-1; /*放置在数字层后面*/}
/*将当前/完成步骤之前连接线变绿*/
.steps li.active:after {background-color: #3db6f2;}
.steps li.active {color:#3db6f2}
/*将当前/完成步骤之后的数字及连接线变灰*/
.steps li.active ~ li:before,
.steps li.active ~ li:after {background-color: #ccc;}

/**到底啦没有更多啦**/
.exactly{text-align: center;padding:0.5 0 1rem 0;}
.exactly .iconfont{font-size:4rem; margin-bottom:.5rem; display: inline-block;}

/**无数据提示**/
.countless{width: 100%; margin: 0 auto; text-align: center;}
.countless .iconfont{font-size:18rem; color:#ccc}
.countless p{color:#ccc}


/**我的订单**/
.order-tab{ width: 100%; background: #fff; margin-bottom: 1rem;}
.order-tab .head{ width: 100%;overflow: hidden; background:#fcfcfc;padding:1.5rem 1rem; display: block;box-sizing: border-box;border-top: 1px solid #eee;}
.order-tab .head span{ display: inline-block;}
.order-tab .head span:last-child{float: right;}
.order-tab .order-body{ width:100%; display: flex; padding: 1.5rem 1rem;border-bottom: 1px solid #eee;border-top: 1px solid #eee;}
.order-left{ width:30%;  display: block;overflow: hidden; padding-right:1rem;}
.order-left img{width: 100%; display: block; height: 100%;border-radius: 5px;}
.order-right{ width:70%;}
.order-right h3{font-size: 1.8rem;text-overflow: ellipsis; overflow: hidden; white-space: nowrap; margin-bottom: 1rem;}
.order-right p{ color: #999;}
.order-right .addres{ margin-bottom: 1rem;text-overflow: ellipsis; overflow: hidden;white-space: nowrap;}
.order-right .addres .iconfont{color: #e0e0e0; font-size: 1.4rem;}

/**无更多订单**/
.order-more{text-align: center; color:#ccc;position: relative; padding: 1rem 0;}
.order-more:after{content:" ";position: absolute;left: 15px;right:65%;top:1.7rem;height: 1px;border-top: 1px solid #999;color: #e5e5e5;-webkit-transform-origin: 0 0;transform-origin: 0 0; -webkit-transform: scaleY(0.5);transform: scaleY(0.5);z-index: 2;}
.order-more:before{content:" ";position: absolute;left: 65%;right:0;top:1.7rem;width:30%;height: 1px;border-bottom: 1px solid #999;color: #e5e5e5;-webkit-transform-origin: 0 0;transform-origin: 0 0;-webkit-transform: scaleY(0.5);transform: scaleY(0.5);z-index: 2;}

/**订单号***/
.order-phone{background: #fff; height: 50px; line-height: 50px; margin: 0 auto; position: relative;}
.order-phone .z-map:before{content:" ";position: absolute;left:50%;right:0;top:1.6rem;width:4%;height: 1px;border-bottom: 1px solid #d2d2d2;color: #d2d2d2;-webkit-transform-origin: 0 0;transform-origin: 0 0;-webkit-transform: scaleY(0.5);transform: scaleY(0.5);transform:rotate(7deg);
-ms-transform:rotate(90deg); 	/* IE 9 */
-moz-transform:rotate(90deg); 	/* Firefox */
-webkit-transform:rotate(90deg); /* Safari 和 Chrome */
-o-transform:rotate(90deg); 	/* Opera */}
.order-detalis-list{ margin-top: 1rem;}

/**卡片订单详情**/
.card-details-box{margin-top:4rem;}
.card-detalis-code .infos{ width: 100%; overflow: hidden; display:block;text-align: center;font-size: 1.6rem; padding: 1.2rem 0; color: #999;}
