/**服务项目***/
.services-box{width:100%; display:block;margin:3.5rem auto 0; overflow-y: auto; }
.services-list{position: relative; width:95%;overflow: hidden; margin: 0 auto; margin-bottom: 1rem; display:block;}
.services-list .title-box{position: relative;display:flex; z-index:8;}
.services-list .title{background: #36b8fd; width:90px; height:30px; line-height:30px;font-size: 1.6rem; margin: 0; padding:0; color: #fff;}
