.services-list li{border-bottom:1px solid #36b8fd; overflow: hidden; height: 50px; line-height: 50px; position: relative;}

.services-list li .services-left{float: left; width:30%;font-size: 1.6rem; overflow: hidden; text-overflow: ellipsis; white-space: nowrap;}
.services-list li .services-body{float: left;width:50%; text-align:right;box-sizing: border-box;font-size: 1.6rem;}
.services-list li .services-right{float: right;width:15%; padding-left:1rem; position: relative; text-align: center;}
.services-list li .services-body .price{font-size: 1.6rem; color: #fd6f37;}
