.services-btn{ width: 95%; margin: 1rem auto; height:5rem; line-height:5rem;}

/**商家详情服务项目**/
.details-services-title{box-sizing: border-box; width: 100%; padding-left: 1rem; text-align:left; height: 5rem; line-height: 5rem; font-size: 1.8rem; border-top:1px solid #eee;border-bottom:1px solid #eee;margin: 1rem auto; background: #fff;}

/**选择卡片**/
.select-conter{ width: 100%; height: 100%; background: #fff; margin-top:18rem;}
